import Vue from 'vue'
import Router from 'vue-router'
import adminInfo from '@/components/page/admin_info'
import goodsCategory from '@/components/page/goods_category'
import goodsLists from '@/components/page/goods_lists'
import addGoods from '@/components/page/add_goods'
import userLists from '@/components/page/user_lists'
import store from '@/store'
import Hi from '@/components/common/Hi'
import mainPage from '@/components/mainPage'
import login from '@/components/login'
import _user from '@/service/user'
Vue.use(Router)
const VueRouter = new Router({
  routes: [
    {
      path: '/mainPage',
      name: 'mainPage',
      component:mainPage,
      children:[
      	{
	      path: 'admin_info',
	      name: 'adminInfo',
	      meta:{
	      	auth:true
	      },
	      component:adminInfo,
    	},
    	{
	      path: 'goods_category',
	      meta:{
	      	auth:true
	      },
	      name: 'goodsCategory',
	      component:goodsCategory
	    },
	    {
	      path: 'goods_lists',
	      meta:{
	      	auth:true
	      },
	      name: 'goodsLists',
	      component:goodsLists
	    },
	    {
	      path: 'add_goods',
	      meta:{
	      	auth:true
	      },
	      name: 'addGoods',
	      component:addGoods
	    },
	    {
	      path: 'user_lists',
	      meta:{
	      	auth:true
	      },
	      name: 'userLists',
	      component:userLists
	    }
      ]
    },
    {
      path: '/',
      name: 'login',
      component:login  	
    }
  ]
});

